import Vue from 'vue'
import Router from 'vue-router'
import Questions from '@/components/Questions'
import ShowPics from '@/components/ShowPics'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Questions',
      component: Questions
    },
    {
      path: '/show',
      name: 'ShowPics',
      component: ShowPics
    },
  ]
})



