<template>
  <div class="mask">
    <div class="content">
      <h1>Our wedding date is:</h1>
      <el-form :model="formData" ref="numberValidateForm" label-width="0">
      <el-form-item
        prop="sendDate"
        :rules="rules"
      >
      <el-date-picker
        type="date"
        placeholder="please select the date"
        v-model="formData.sendDate"
        format="yyyy-MM-dd"
        value-format="yyyy-MM-dd"
      ></el-date-picker>
      </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: "Questions",
  data() {
    return {
      formData: {
        sendDate: "",
      },
      errInfo: [
        "don't u remeber our wedding date?",
        "wao, amazing~!!!!",
        "u can hardly guest it ar~~",
        "gan ba die nie. ^_^",
        "Zai cai bu chulai wo jiu....",
        "I say the tips is: 20xx-xx-xx",
        "Wo dengdao huaEr ye xieniao",
        "haoba,jixu jiayou",
        ".......",
        "*(&*^&%^&$*&%&!@@+_"
      ],
      rules: [
        { 
          required: false, validator: (rule, value, callback) => {
            const randomIndex = parseInt(Math.random() * 9),
              info = this.errInfo[randomIndex]
            if (value !== '2012-02-09') {
              callback(new Error(info))
            } else {
              callback()
            }
          }, 
          trigger: 'change'
        }
      ]
    };
  },
  watch: {
    'formData.sendDate'(value) {
      if (value === "2012-02-09") {
        localStorage.isQualify = 1
        this.$router.push("/show");
      }
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.content {
  width: 90%;
  padding-top: 30px;
  margin: 0px auto;
 
}

.content  h1 {
  font-family: 'Audiowide';
  color:#ACB6E5;
  padding: 10px 0;
  background: -webkit-linear-gradient(90deg,#ACB6E5,#86FDE8);
  -webkit-text-fill-color: transparent;
  text-shadow: h-shadow v-shadow blur color;
  background-image: -webkit-linear-gradient(92deg, #f35626, #feab3a);
  -webkit-background-clip: text;
  -webkit-animation: hue 30s infinite linear;
}

@-webkit-keyframes hue {
  from {
    -webkit-filter: hue-rotate(0deg);
  }
  to {
    -webkit-filter: hue-rotate(-360deg);
  }
}

.mask {
  width: 100%;
  height: 100%;
  background: url('../assets/bkg1.png') center center no-repeat;
  background-size: 390px 844px;
}
</style>



